@import '../styles/settings';

.toolkit-prototype {
  .ide-home-heading-container {
    max-width: 500px;
    display: flex;
    align-items: center;
  }

  .ide-home-header {
    flex-wrap: nowrap;
  }

  .ide-home-table {
    background-color: transparent;
  }

  .ide-card {
    margin: .1rem;
  }

  .ide-home-image-container {
    height: 100%;
    max-width: 100%;
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .ide--home-image {
    padding-bottom: $spacing-08;
    max-width: 450px;
    width: 100%;
    height: auto;
  }

  .ide-home-product-heading {
    font-size: 1.25rem;
    line-height: 1.375;
    font-weight: 600;
  }

  .ide-home-call-to-action {
    font-size: 1.25rem;
    font-weight: 400;
    line-height: 1.4;
    letter-spacing: 0;
  }
}
